<template>
  <div id="login_box">
    <div class="login_logo">停车云平台</div>
    <div class="login_con" style="">
    	<div class="login_form" style="">
    		<span class="hydl">欢迎登录</span>
    		<el-form :model="login"  label-width="0px" style="padding: 20px 0;"  v-loading.fullscreen.lock="loginLoading"   element-loading-text="登录中...">
    			<el-form-item >
                    <el-input v-model="login.username" placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item >
                    <el-input type="password" placeholder="密码" v-model="login.password"
                              @keyup.enter.native="loginFn()"></el-input>
                </el-form-item>
                <el-button type="button" @click="loginFn()" style="width: 47%" class="el-button el-button--primary">登录</el-button>
				<el-button type='button' @click="register()" class="el-button el-button--success" style="width:47%">注册</el-button>	
			</el-form>
    	</div>
	</div>
	<!-- 用户注册模态框 -->
	<el-dialog title='用户注册页面' :visible.sync="dialogVisible" style="width:70%;" >
		<el-form :model='userMsg' :inline="true" :label-position='labelPosition' label-width="100px" :rules="rules"
		v-loading.fullscreen.lock="loading"    element-loading-text="拼命加载中...">
			<el-form-item label='登录账号' prop="username">
				<el-input v-model='userMsg.username' placeholder="请输入登录账号"></el-input>
			</el-form-item>
			<el-form-item label='登录密码' prop="password">
				<el-input v-model='userMsg.password' type="password"></el-input>
			</el-form-item>
			<el-form-item label='姓名' prop='name'>
				<el-input v-model='userMsg.name' placeholder="请输入姓名"></el-input>
			</el-form-item>
			<el-form-item label='身份证号码' prop='cardNo'>
				<el-input v-model='userMsg.cardNo' placeholder="请输入身份证号码"></el-input>
			</el-form-item>
			<el-form-item label='手机号' prop='phone'>
					<el-input v-model='userMsg.phone' placeholder="请输入手机号"></el-input>
				</el-form-item>
				<el-form-item label="性别" style="position:relative;right:40px;" prop='gender'>
						<el-radio-group v-model="userMsg.gender">
							<el-radio :label='1'>男</el-radio>
							<el-radio :label='2'>女</el-radio>
						</el-radio-group>
				</el-form-item>
				<br>
			</el-form-item>
				<el-button type='primary' style="width:80%" @click='handleSendUser()' >提交</el-button>
			</el-form-item>
		</el-form>
	</el-dialog>


  </div>
</template>

<script>
export default {
	data () {
		var validatorInput=function(rule,value,callback){
			if(!value){
				callback(new Error(rule.message))
			}
			}
		return {
			login:{

			},
			rules:{
				username:[{required: true, message: '请输入帐号', trigger: 'blur',validator:validatorInput}],
				password:[{required: true, message: '请输入密码', trigger: 'blur',validator:validatorInput }],
				name:[{required: true, message: '请输入姓名', trigger: 'blur',validator:validatorInput }],
				gender:[{required: true, message: '请选择性别', trigger: 'blur',validator:validatorInput}],
				phone:[{required: true, message: '请输入手机', trigger: 'blur',validator:validatorInput}],
				cardNo:[{required: true, message: '请输入身份证', trigger: 'blur',validator:validatorInput}],
			},
			dialogVisible:false,//显示注册页面
			userMsg:{},
			xbVal:"",
			labelPosition:"left",
			loading:false,
			loginLoading:false
		}
	},
	mounted:function() {
	},
	methods:{
	    loginFn(){
			let self = this;
			let url = self.baseUrl + "sysUser/login";
			var params={
				'loginAccount':self.login.username,
				'loginPassword':self.login.password
			}
			if(!self.login.username || !self.login.password){
				self.$message({
					message:"请输入账户信息",
					type:"warning"
				})
				return;
			}
			this.loginLoading=true;
			this.$axios.post(url,params).then(function(res){
				if(res.data.status=='0'){
					self.loginLoading=false;
					self.$message({
						message:res.data.message,
						type:"success"
					})
					localStorage.setItem("userId",res.data.data.id)
					localStorage.setItem("userName",res.data.data.userName)
					self.$router.push({path:"/testMsg"})
				}else{
					console.log(this)
					self.loginLoading=false;
					self.$message({
						message:res.data.message,
						type:"error"
					})
				}
			}).catch(function(err){
				console.log(err);
			})
		},
		register(){
			this.dialogVisible=true;
		},
		handleSendUser(){
			var dataArr=[];
			var self =this;
			var params={
				"loginAccount":this.userMsg.username,
				"loginPassword":this.userMsg.password,
				"userName":this.userMsg.name,
				"userGender":this.userMsg.gender,
				"userPhone":this.userMsg.phone,
				"userCardNo":this.userMsg.cardNo
			}
			
			Object.keys(params).forEach(function(key){
				dataArr.push(params[key])
			})
			for(var i=0;i<dataArr.length;i++){
				if(!dataArr[i]){
					self.$message({
						message:"必填项未填写，请检查输入!",
						type:"warning"
					})
					return;
				}
			}
			self.loading=true;
				var url=this.baseUrl +"sysUser/register";
				this.$axios.post(url,params).then(function(res){
					if(res.data.status=='0'){
						self.dialogVisible=false;
						self.loading=false;
						self.$notify({
								type:"success",
								message:"注册成功"
							});
							// self.$router.push({path:"/login"});
					}else{
						self.dialogVisible=true;
						self.$notify({
							message:res.data.message,
							type:"error"
						})
						self.loading=false;
					}
				}).catch(function(err){
					console.log(err);
				})

		}
	}
}
</script>

<style scoped>
#login_box{
    width: 100%;
    height: 100%;
    /* background: #0c2a61; */
    background:url("../../../static/img/bg.png") no-repeat;
    overflow: hidden;
}
.login_con{
	max-width: 1100px;
	height: 100%;
    /* background: url("../../../static/img/index.png") no-repeat left top; */
    background-size: 100% auto;
    margin: 10px auto 0;
    position: relative;
}
.login_logo{
	max-width: 1100px;
	margin: 30px auto;
	font-size: 32px;
	color: #fff;
	/* background: url("../../../static/img/web12.png") no-repeat 0px -5px; */
	background-size: auto 100%;
	text-align: left;
	height: 70px;
	line-height: 42px;
	padding-left: 80px;
}
.login_form{
	width: 300px;
	height: 240px;
	padding: 40px 40px;
	/* background: rgba(15, 53, 124, 0.8); */
	background: rgba(0, 0, 0, 0.3);
    position: absolute;
    right: 5%;
    top: 5%;
    /* margin-top: -220px; */
    border-radius: 4px;
    box-shadow: 5px 5px 5px #0c285c;
}
.hydl{
	color: #fff;
	font-size: 24px;
	text-align: center;
}
</style>



